@font-face {
  font-family: alrightSans;
  src: url(./alright.otf);
}

.name {
  font-size: 90px;
  font-weight: 100;
  margin: 0;
}

.container {
  position: relative;
  width: 1180px;
  margin: 200px auto;
}

.bio {
  display: inline-block;
}

.intro {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;

  position: relative;
  margin: 0;
  vertical-align: middle;
}

.circle {
  width: 5px;
  height: 5px;
  border-radius: 5px;
  margin: 10px;
}

.intro-text {
  float: left;
  text-align: left;
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.creative {
  background-color: #2196F3;
}

.logic {
  background-color: #E91E63;
}


.app {
  font-family: alrightSans;
  margin: 0;
}

.project-container {
  margin-top: 100px;
}

.project-title-container {
  /* border: 2px solid #000000; */
  background-color: #EEEEEE;
  display: inline-block;
  padding: 5px 20px;
}

.project-container-title {
  display: inline;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 200;
  font-size: 20px;
  margin: 0;
  color: #9E9E9E;
}

.project-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.project-list::after {
  content: " ";
  display: block; 
  height: 0; 
  clear: both;
}

.project-list-item {
  width: 45%;
  float: left;
  margin: 5% 5% 0 0;
}




