.project {
    width: 100%;
    max-height: 700px;
    background-color: #FFFFFF;
    border: 1px solid #edeeee;
    border-radius: 5px;
    overflow: hidden;
}

.image-container {
    height: 250px;
    overflow: hidden;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #edeeee;
}

.image {
    width: 100%;
}

.project-detail-container {
    margin: 20px 20px;
}

.project-title {
    margin: 0;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 500;
}

.learn-more-btn {
    padding: 5px;
    border: 1px solid #BDBDBD;
    color: #BDBDBD;
    overflow: hidden;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 12px;
    font-weight: 400;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 0;
}

.learn-more-btn:hover {
    background-color: #F5F5F5;
}


